html, body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

header {
  font-size: 1.2rem;
}

#logo {
  font-size: 2em;
  font-weight: 300;
  font-family: "Futura";
}

@font-face {
  font-family: "Hanken Grotesk";
  src: url(/static/media/HankenGrotesk-Regular.8aa432c0.eot);
  src:url(/static/media/HankenGrotesk-Regular.a3d1b1bb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Regular.9e8bb12f.otf)format("opentype")
}

@font-face {
  font-family: "Hanken Grotesk Black";
  src: url(/static/media/HankenGrotesk-Black.499224d6.eot);
  src:url(/static/media/HankenGrotesk-Black.ea8cbdeb.woff)format("woff"),
  url(/static/media/HankenGrotesk-Black.616709b8.otf)format("opentype")
}

#about-text p {
  font-family: "Hanken Grotesk";
  font-size: 8vw;
  font-weight: bold;
  color: white;
  text-align: center;
}

#about-text {
  padding-top: 15%;
}

#about-row {
  height: 90vh;
}

#landing-row p {
  color: white;
  font-family: "Hanken Grotesk Black";
  font-size: 4vw;
  padding-bottom: 25%;
}

#landing-row a {
  font-family: "Hanken Grotesk";
  font-size: 4vw;
  padding-bottom: 5%;
  color: white;
}

#landing-row a:hover {
  text-decoration: underline;
}

#landing-row {
  height: 90vh;
}

#categories {
  padding: 5%;
  background-color: #5D486D;
}

#side-img img {
  padding: 5%;
  height: 90vh;
  width: 75vh;
}

#text-description {
  height: 55vh;
  color: black;
  text-align: center;
  padding-left: 20%;
  padding-right: 20%;
  padding-top: 5%;
  padding-bottom: 5%;
}

#text-description h2 {
  font-family: "Hanken Grotesk Black";
  font-size: 4vw;
}

#text-description p {
  font-family: "Hanken Grotesk";
  font-size: 3vw;
}

